import React from "react";

import Link from "gatsby-link";
import Helmet from "react-helmet";

import favicon from '../img/favicon.png';
import logo from '../img/mf.jpg';

const ListLink = props =>
    <li style={{ display: `inline-block`, marginRight: `1rem` }}>
        <Link to={props.to}>
            {props.children}
        </Link>
    </li>

export default ({ children, data }) => (
    <div style={{ margin: `0 auto`, maxWidth: 650, padding: `1.25rem 1rem` }}>
        <Helmet
            title={data.site.siteMetadata.title}
            meta={[
                { charset: 'utf8' },
                { name: 'description', content: 'Minha página pessoal' },
                { name: 'keywords', content: 'marllon, frizzo' },
                { name: 'author', content: 'Marllon Frizzo' },
                { name: 'viewport', content: 'width=device-width, initial-scale=1.0' },
                { property: 'og:title', content: `${data.site.siteMetadata.title}` },
                { property: 'og:url', content: 'https://www.mfrizzo.com.br' },
                { property: 'og:image', content: `${logo}` },
                { property: 'og:type', content: 'website' },
            ]}
            link={[
                { rel: 'shortcut icon', type: 'image/png', href: `${favicon}` },
            ]}
        />
        <header style={{ marginBottom: `1.5rem` }}>
            <Link to="/" style={{ textShadow: `none`, backgroundImage: `none` }}>
                <h3 style={{ display: `inline` }}>Marllon Frizzo</h3>
            </Link>
            <ul style={{ listStyle: `none`, float: `right` }}>
                <ListLink to="/">Início</ListLink>
                <ListLink to="/projetos/">Projetos</ListLink>
                <ListLink to="/sobre/">Sobre</ListLink>
            </ul>
        </header>
        {children()}
    </div>
);

export const query = graphql`
  query SiteTitleQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`



